<template>
    <div id="view-employee">
     <ul class="collection with-header">
        <li class="collection-header">
            <h4>{{name}}</h4></li>
        <li class="collection-item">Employee ID : {{employee_id}}</li>
        <li class="collection-item">Department : {{department}}</li>
        <li class="collection-item">Email : {{email}}</li>
        <li class="collection-item">Phone : {{phone}}</li>      
     </ul>
     <router-link to="/" class="btn grey">Back</router-link>
     <button class="btn red" @click="deleteEmployee">Delete</button>

     <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}"
       class="btn-floating btn-large red">
        <i class="material-icons">edit</i>
         </router-link>
     </div>

    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-employee',
    data () {
        return {
            employee_id: null,
            name: null,
            department: null,
            email: null,
            phone: null
        }
    },
    beforeRouteEnter(to, from, next) {
db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapshot => {
    querySnapshot.forEach(doc => {
        next(vm => {
            vm.employee_id = doc.data().employee_id
            vm.name = doc.data().name
            vm.department = doc.data().department
            vm.email = doc.data().email
            vm.phone = doc.data().phone
        })
    })
})
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('employees').where
            ('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.employee_id = doc.data().employee_id
                    this.name = doc.data().name
                    this.department = doc.data().department
                    this.email = doc.data().email
                    this.phone = doc.data().phone
                })
            })
        },
        deleteEmployee() {
            if(confirm("Are you sure?")) {
                 db.collection('employees').where
            ('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                 doc.ref.delete()
                 this.$router.push('/')
                
             })

           })
        }
    }
  } 
}
</script>




