<template>
    <div id="Employees">
    <ul class="collection with-header">
        <li class="collection-header"><h4>Employees</h4></li>
        <li v-for="employee in employees" v-bind:key="employee.id" 
        class="collection-item">
          <div class="chip">{{employee.department}}</div> {{employee.employee_id}} : 
          {{employee.name}} : <a href="'mailto:' + employee.email">{{employee.email}}</a>
           : {{employee.phone}}

           <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
            <i class="material-icons">info</i>
           </router-link>
        </li>
    </ul>        
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
            <i class="material-icons">add</i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'employees',
    data () {
        return {
         employees: []
        }
    },
    created () {
        db.collection('employees').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'department': doc.data().department,
                    'email': doc.data().email,
                    'phone': doc.data().phone

                }
                this.employees.push(data)
            })
        })
    }
}
</script>




