<template>
    <div id="edit-employee">
        <div class="row">
    <div class="col s12">
    <div class="card">
    <div class="card-action white-text">
    <img src="https://companyassets-ngc01.firebaseapp.com/LogoV4Wide.png" class="img-responsive" alt="Nicholson Contracting" style="max-height:65px;height:50%;width:auto">
    <h5>Edit Employee</h5>
        </div>
        
    <div class="card-content">
    <form @submit.prevent="updateEmployee">
        <div class="row">
           <div class="col s6">
            <div class="form-field">
            <input disabled v-model="employee_id" type="text" required>
            <label>Employee ID#</label>
            </div>
           </div>
          
          <div class="col s6">
           <div class="form-field">
            <input type="text" v-model='name' required>
            <label>Name</label>
            </div>
          </div>
        </div>
          
        <div class="row">
          <div class="col s6">
           <div class="form-field">
            <input type="text" v-model="department" required>
            <label>Department</label>
           </div>
          </div>
          
          <div class="col s6">
           <div class="form-field">
            <input type="text" v-model="email">
            <label>Email</label>
           </div>
          </div>
        </div>
          
        <div class="row">
          <div class="col s6">
           <div class="form-field">
            <input type="text" v-model="phone">
            <label>Phone</label>
           </div>
          </div>
          <br />
         <div class="button">
          <button class="btn-small waves-effect waves-dark right" type="submit">Submit
           <i class="material-icons right">send</i></button>
         <br /><br />
        <div class="row">
          <div class="col s6">
            <div class="button">
             <router-link to="/" class="btn-small right grey">Cancel
             <i class="material-icons right">cancel</i>
             </router-link>
            
            </div>         
          </div>
        </div>
         </div>
         </div>
    </form>
    </div>
    </div>
    </div>
    </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-employee',
    data () {
        return {
            employee_id: null,
            name: null,
            department: null,
            email: null,
            phone: null
        }
    },
       beforeRouteEnter(to, from, next) {
db.collection('employees').where('employee_id', '==', 
to.params.employee_id).get().then(querySnapshot => {
    querySnapshot.forEach(doc => {
        next(vm => {
            vm.employee_id = doc.data().employee_id
            vm.name = doc.data().name
            vm.department = doc.data().department
            vm.email = doc.data().email
            vm.phone = doc.data().phone
        })
    })
  })
},
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('employees').where
            ('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.employee_id = doc.data().employee_id
                    this.name = doc.data().name
                    this.department = doc.data().department
                    this.email = doc.data().email
                    this.phone = doc.data().phone
                })
            })
        },
        updateEmployee () {
         db.collection('employees').where
         ('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        employee_id: this.employee_id,
                        name: this.name,
                        department: this.department,
                        email: this.email,
                        phone: this.phone
                    })
                    .then(() => {
                        this.$router.push({name: 'view-employee', 
                        params: {employee_id: this.employee_id}})
                    })
                })
            })

        }
  }
}
</script>

<style>

body {
    background: #b4b4b4 !important;
}

/*.container{
  max-width:900px;
  margin-left:auto;
  margin-right:auto;
  padding:1em;
}*/


img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}


.card-action{
background-color:#182c48
 !important;
}
</style>



