<template>
    <div id="new-employee">
   <!-- <div class="container">-->
    <div class="row">
    <div class="col s12">
    <div class="card">
    <div class="card-action white-text">
    <img src="https://companyassets-ngc01.firebaseapp.com/LogoV4Wide.png" class="img-responsive" alt="Nicholson Contracting" style="max-height:65px;height:50%;width:auto">
    <h5>New Employee</h5>
        </div>
        
    <div class="card-content">
    <form @submit.prevent="saveEmployee">
        <div class="row">
           <div class="col s6">
            <div class="form-field">
            <input v-model="employee_id" type="text" required>
            <label>Employee ID#</label>
            </div>
           </div>
          
          <div class="col s6">
           <div class="form-field">
            <input type="text" v-model='name' required>
            <label>Name</label>
            </div>
          </div>
        </div>
          
        <div class="row">
          <div class="col s6">
           <div class="form-field">
            <input type="text" v-model="department" required>
            <label>Department</label>
           </div>
          </div>
          
          <div class="col s6">
           <div class="form-field">
            <input type="text" v-model="email">
            <label>Email</label>
           </div>
          </div>
        </div>
          
        <div class="row">
          <div class="col s6">
           <div class="form-field">
            <input type="text" v-model="phone">
            <label>Phone</label>
           </div>
          </div>
          <br />
         <div class="button">
          <button class="btn-small waves-effect waves-dark right" type="submit">Submit
           <i class="material-icons right">send</i></button>
         <br /><br />
        <div class="row">
          <div class="col s6">
            <div class="button">
             <router-link to="/" class="btn-small right grey">Cancel
             <i class="material-icons right">cancel</i>
             </router-link>
            
            </div>         
          </div>
        </div>
         </div>
         </div>
    </form>
    </div>
    </div>
    </div>
    </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-employee',
    data () {
        return {
            employee_id: null,
            name: null,
            department: null,
            email: null,
            phone: null
        }
    },
    methods: {
        saveEmployee () {
            db.collection('employees').add({
                employee_id: this.employee_id,
                name: this.name,
                department: this.department,
                email: this.email,
                phone: this.phone
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(error))
        }
    }
}
</script>

<style>

body {
    background: #b4b4b4 !important;
}

/*.container{
  max-width:900px;
  margin-left:auto;
  margin-right:auto;
  padding:1em;
}*/


img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}


.card-action{
background-color:#182c48
 !important;
}
</style>



